.container{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-image: linear-gradient(180deg, #130754 0%, #3b2f80 100%);
    border-radius: 12px;
    width: 60vw;
    height: 90vh;
}

.top-bar{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
}

.top-bar input{
    outline: none;
    width: 60%;
    height: 50px;
    border-radius: 40px;
    border: none;
    padding-left: 40px;
}

.top-bar .search-icon{
    width: 50px;
    height: 50px;
    background-color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50px;
    margin-left: 20px;
    cursor: pointer;
}

.temp{
    margin-top: -30px;
    font-size: 90px;
    font-weight: 400;
    color: white;
}

.weather-location{
    font-size: 44px;
    color: white;
    margin-bottom: 30px;
}


.data-container{
    display: flex;
    width: 80%;
    justify-content: space-between;
    color: white;
}

.element{
    display: flex;
}

.data {
    margin-left: 10px;
}


@media screen and (max-width: 700px) {
    .container{
        width: 80vw;
    }
}

@media screen and (max-width: 400px) {
    .container{
        width: 100vw;
        height: 10vhx;
    }

    .top-bar input{
        width: 50%;
        height: 50px;
        
    }

    .temp{
        margin-top: -30px;
        font-size: 50px;
        
    }

    .search-icon{
        width: 60%;
    }

    .weather-location{
        font-size: 36px;
        color: white;
        margin-bottom: 30px;
    }

    .top-bar .search-icon{
        width: 40px;
        height: 40px;
        
    }
}